const routerAddress = '0xE3F85aAd0c8DD7337427B9dF5d0fB741d65EEEB5';

const defaultFuse = {
    name: 'Fuse',
    symbol: 'FUSE',
    contract_address: 'native'
}

const defaultCeus = {
    name: 'Coineus',
    symbol: 'CEUS',
    contract_address: '0x4e69Ae0CD024754655b4eF74F24A8DCB39Ba07e8'
}

const PATHS = {
    FUSE_CEUS: [
        '0x0be9e53fd7edac9f859882afdda116645287c629',
        '0x4e69ae0cd024754655b4ef74f24a8dcb39ba07e8'
    ],
    FUSE_LIT: [
        '0x0be9e53fd7edac9f859882afdda116645287c629',
        '0x4e69ae0cd024754655b4ef74f24a8dcb39ba07e8',
        '0xF2C6C1AA2bf8ec40F564Ea8A483F64907ea37A3F'
    ],

    CEUS_FUSE: [
        '0x4e69ae0cd024754655b4ef74f24a8dcb39ba07e8',
        '0x0be9e53fd7edac9f859882afdda116645287c629'
    ],
    CEUS_LIT: [
        '0x4e69ae0cd024754655b4ef74f24a8dcb39ba07e8',
        '0xF2C6C1AA2bf8ec40F564Ea8A483F64907ea37A3F'
    ],

    LIT_FUSE: [
        '0xF2C6C1AA2bf8ec40F564Ea8A483F64907ea37A3F',
        '0x4e69ae0cd024754655b4ef74f24a8dcb39ba07e8',
        '0x0be9e53fd7edac9f859882afdda116645287c629'
    ],
    LIT_CEUS: [
        '0xF2C6C1AA2bf8ec40F564Ea8A483F64907ea37A3F',
        '0x4e69ae0cd024754655b4ef74f24a8dcb39ba07e8'
    ]
}

export default function Swap() {

    const {wallet, rpc} = useSelector(state => state.coineus);

    const [selectedInputToken, setSelectedInputToken] = useState(defaultFuse);

    const [selectedOutputToken, setSelectedOutputToken] = useState(defaultCeus);

    const [selectFocus, setSelectFocus] = useState('input');

    const [amountIn, setAmountIn] = useState(0);
    const [amountOut, setAmountOut] = useState(0);
    const [slippage, setSlippage] = useState(.995);
    const [pending, setPending] = useState(false);
    const [amtPending, setAmtPending] = useState(false);

    const [amtSent, setAmtSent] = useState(undefined);
    const [amtRecieved, setAmtRecieved] = useState(undefined);
    const [txHash, setTxHash] = useState(undefined);
    const [txCost, setTxCost] = useState(undefined);

    const getRoute = () => {
        const route = selectedInputToken.symbol + "_" + selectedOutputToken.symbol;
        return PATHS[route];
    }

    const getAmountsOut = () => {
        if (toWei(amountIn) === 'NaN') return;
        if (selectedInputToken.symbol === selectedOutputToken.symbol) return;
        if (selectFocus === 'output') return;

        const web3 = new Web3(rpc.fuse);
        const contract = new web3.eth.Contract(router_abi, routerAddress);

        setAmtPending(true);

        contract.methods.getAmountsOut(toWei(amountIn), getRoute()).call((err, resp) => {
            resp ? setAmountOut((resp[resp.length - 1] / (10 ** 18)).toFixed(8)) : setAmountOut(0);
            setAmtPending(false);
        })
    }

    const getAmountsIn = () => {
        if (toWei(amountOut) === 'NaN') return;
        if (selectedInputToken.symbol === selectedOutputToken.symbol) return;
        if (selectFocus === 'input') return;

        const web3 = new Web3(rpc.fuse);
        const contract = new web3.eth.Contract(router_abi, routerAddress);

        setAmtPending(true);

        contract.methods.getAmountsIn(toWei(amountOut), getRoute()).call((err, resp) => {
            resp ? setAmountIn((resp[0] / (10 ** 18)).toFixed(8)) : setAmountIn(0);
            setAmtPending(false);
        })
    }

    useEffect(getAmountsOut, [amountIn, selectedOutputToken]);
    useEffect(getAmountsIn, [amountOut, selectedInputToken]);

    useEffect(() => {
        //handle selecting same token
        if (selectedInputToken.symbol === selectedOutputToken.symbol) {
            if (selectFocus === 'input') {
                if (selectedInputToken.symbol === 'FUSE') {
                    setSelectedOutputToken(defaultCeus)
                } else {
                    setSelectedOutputToken(defaultFuse)
                }
            } else {
                if (selectedOutputToken.symbol === 'FUSE') {
                    setSelectedInputToken(defaultCeus)
                } else {
                    setSelectedInputToken(defaultFuse)
                }
            }
        }
    }, [selectedInputToken, selectedOutputToken])

    const swap = async () => {

        setAmtSent(undefined);
        setAmtRecieved(undefined);
        setTxHash(undefined);
        setTxCost(undefined);

        const web3 = new Web3(window.provider);

        const contract = new web3.eth.Contract(router_abi, routerAddress);
        const gasPrice = await web3.eth.getGasPrice();

        let method;

        if (selectedInputToken.symbol === 'FUSE') {
            console.log(selectFocus === 'input' ? 'swapExactETHForTokens' : 'swapETHForExactTokens');
            method = contract.methods[selectFocus === 'input' ? 'swapExactETHForTokens' : 'swapETHForExactTokens'](
                toWei(amountOut * slippage),
                getRoute(),
                wallet.address,
                (((new Date()).getTime() + 60000) / 1000).toFixed(0)
            ).send(
                {
                    from: wallet.address,
                    value: web3.utils.toWei(amountIn),
                    gas: 300000,
                    gasPrice
                }
            )

        } else if (selectedOutputToken.symbol === 'FUSE') {
            console.log(selectFocus === 'input' ? 'swapExactTokensForETH' : 'swapTokensForExactETH');

            method = contract.methods[selectFocus === 'input' ? 'swapExactTokensForETH' : 'swapTokensForExactETH'](
                toWei(amountIn),
                toWei(amountOut * slippage),
                getRoute(),
                wallet.address,
                (((new Date()).getTime() + 60000) / 1000).toFixed(0)
            ).send(
                {
                    from: wallet.address,
                    gas: 300000,
                    gasPrice
                }
            )

        } else {
            console.log(selectFocus === 'input' ? 'swapExactTokensForTokens' : 'swapTokensForExactTokens');

            method = contract.methods[selectFocus === 'input' ? 'swapExactTokensForTokens' : 'swapTokensForExactTokens'](
                toWei(amountIn),
                toWei(amountOut * slippage),
                getRoute(),
                wallet.address,
                (((new Date()).getTime() + 60000) / 1000).toFixed(0)
            ).send(
                {
                    from: wallet.address,
                    gas: 300000,
                    gasPrice
                }
            );
        }

        method.once('transactionHash', function (tx) {
            setPending(true);
            setTxHash(tx);
        })
            .on('error', function (error) {
                setPending(false)
            })
            .on('receipt', function(receipt) {
                console.log('receipt', receipt.events.Transfer);
                setAmtSent(receipt.events.Transfer[0].returnValues.value / (10 ** 18));
                setAmtRecieved(receipt.events.Transfer[receipt.events.Transfer.length - 1].returnValues.value / (10 ** 18));
            })
            .then(function (receipt,a,b) {
                getUserBalances(wallet.address);
                setTxCost(receipt.gasUsed * (receipt.effectiveGasPrice / (10 ** 18)));
                setPending(false);
            });
    }

    const swapInputOutputTokens = () => {
        const inToken = {...selectedInputToken};
        const outToken = {...selectedOutputToken};
        // const inTokenAmt = amountIn;
        // const outTokenAmt = amountOut;
        setSelectedInputToken(outToken);
        setSelectedOutputToken(inToken);
        // setAmountIn(outTokenAmt);
        // setAmountOut(inTokenAmt);
    }

    return (<Grid container spacing={2} style={{marginTop: -5, paddingBottom: 20}}>
            <Grid item xs={12}>
                <Paper style={{padding: 20}}>
                    <Grid container spacing={2}>
                        <Grid item xs={12} onFocus={() => setSelectFocus('input')}>
                            <Typography variant="caption"
                                        style={{float: 'right'}}>Balance: {CoineusCryptoFormat(wallet.balances.fuse[selectedInputToken.contract_address])}</Typography>
                            <SwapInput
                                value={amountIn || ""}
                                onChange={(ev) => {
                                    if(amtPending)return;
                                    setAmountIn(ev.target.value)
                                }}
                                startAdornment={<TokenList selectedToken={selectedInputToken}
                                                           setSelectedToken={setSelectedInputToken}
                                />}
                            />
                        </Grid>
                        <Grid item xs={12} alignContent="center" style={{textAlign: 'center', height: 0, paddingTop: 0}}>
                            <Fab size="small" variant="contained" color="primary" style={{marginTop: -12}} onClick={swapInputOutputTokens}>
                                <SwapVertIcon/>
                            </Fab>
                        </Grid>
                        <Grid item xs={12} onFocus={() => setSelectFocus('output')}>
                            <SwapInput
                                value={amountOut || ""}
                                onChange={(ev) => {
                                    if(amtPending)return;
                                    setAmountOut(ev.target.value)
                                }}
                                startAdornment={<TokenList selectedToken={selectedOutputToken}
                                                           setSelectedToken={setSelectedOutputToken}
                                />}
                            />
                            <Typography variant="caption"
                                        style={{float: 'right'}}>Balance: {CoineusCryptoFormat(wallet.balances.fuse[selectedOutputToken.contract_address])}</Typography>
                        </Grid>
                        <Grid item xs={12}>
                            <NetworkButton network={CHAIN_IDS.FUSE}>
                                <Button fullWidth variant="contained" onClick={swap}>SWAP</Button>
                            </NetworkButton>
                        </Grid>
                    </Grid>
                </Paper>
                {
                    amtRecieved !== undefined && <>
                        <Divider style={{marginTop: 40, marginBottom: 10}}>RECEIPT</Divider>
                        <Paper
                            style={{
                                backgroundColor: 'white',
                                color: "#000",
                                padding: 20,
                                fontFamily: 'monospace'
                            }}>
                            <Grid container>
                                <Grid item xs={6}>
                                    <Typography style={{fontFamily: 'monospace'}}>Spent</Typography>
                                </Grid>
                                <Grid item xs={6}>
                                    <Typography style={{fontFamily: 'monospace', textAlign: 'right'}}>
                                        {`${CoineusCryptoFormat(amtSent)} ${selectedInputToken.symbol}`}
                                    </Typography>
                                </Grid>

                                <Grid item xs={6}>
                                    <Typography style={{fontFamily: 'monospace'}}>Received</Typography>
                                </Grid>
                                <Grid item xs={6}>
                                    <Typography style={{fontFamily: 'monospace', textAlign: 'right'}}>
                                        {`${CoineusCryptoFormat(amtRecieved)} ${selectedOutputToken.symbol}`}
                                    </Typography>
                                </Grid>

                                <Grid item xs={12} style={{marginTop: 20}}/>


                                <Grid item xs={6}>
                                    <Typography style={{fontFamily: 'monospace'}}>TxFee</Typography>
                                </Grid>
                                <Grid item xs={6}>
                                    <Typography style={{fontFamily: 'monospace', textAlign: 'right'}}>
                                        {`${CoineusCryptoFormat(txCost)} FUSE`}
                                    </Typography>
                                </Grid>


                                <Grid item xs={6}/>
                                <Grid item xs={6}>
                                    <Typography style={{fontFamily: 'monospace', textAlign: 'right'}}>
                                        {`${CoineusUSDFormat(txCost * wallet.prices.fuse?.native?.inUSD)} USD`}
                                    </Typography>
                                </Grid>


                                <Grid item xs={12} style={{marginTop: 20}}/>

                                <Grid item xs={6}>
                                    <Typography style={{fontFamily: 'monospace'}}>TxHash</Typography>
                                </Grid>
                                <Grid item xs={6}>
                                    <Typography style={{fontFamily: 'monospace', textAlign: 'right'}}>

                                        <Link
                                            style={{color: "#000", textDecorationColor: '#000'}}
                                            href={`https://explorer.fuse.io/tx/${txHash}`}
                                            target="_blank">{`0x..${txHash.substr(-12)}`}</Link>
                                    </Typography>
                                </Grid>
                            </Grid>

                        </Paper>
                    </>
                }
            </Grid>
            <CoineusLoading open={pending} label="Transaction Pending"/>
        </Grid>
    )
}